import React from "react";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import AOS from "aos";
import "aos/dist/aos.css";
import dayVideo from "../components/assets/LotteryVideo/day.mp4";
import afternoonVideo from "../components/assets/LotteryVideo/afternoon.mp4";
import nightVideo from "../components/assets/LotteryVideo/night.mp4";
import LotteryPageNew from "./LotterypageData";
import Header from "./Header";

const OtherPage = () => {
  AOS.init();

  let currentTime = new Date().getHours();

  const isOpenFunc = () => {
    return console.log("");
  };

  return (
    <div className="backClr">
      <div
        data-aos="zoom-in"
        data-aos-anchor="#example-anchor"
        data-aos-offset="500"
        data-aos-duration="300"
        style={{
          overflow: "hidden",
          backgroundColor: "black",
          backgroundSize: "cover",
          width: "100%",
          height: "100%",
          objectFit: "contain",
          position: "fixed",
        }}
      >
        <video
          style={{
            position: "fixed",
            width: "inherit",
            objectFit: "cover",
            height: "100vh",
            transform: "none",
          }}
          autoPlay
          loop
          muted
        >
          <source
            src={
              currentTime >= 8 && currentTime <= 12
                ? dayVideo
                : currentTime > 12 && currentTime <= 18
                ? afternoonVideo
                : nightVideo
            }
            type="video/mp4"
          />{" "}
        </video>
        <div>
          <div
            style={{
              height: "100vh",
              width: "100vw",
              position: "absolute",
              backgroundColor: "black",
              opacity: ".7",
            }}
          ></div>
          <div
            style={{ height: "100vh", width: "100vw", position: "absolute" }}
          >
            <Header isOpenFunc={isOpenFunc} isOpen={false} pageName="lottery" />
            <LotteryPageNew />
          </div>
        </div>
      </div>
    </div>
  );
};

export default OtherPage;
