import React, { useEffect } from "react";
import { Link } from "react-router-dom";
import "./Header.scss";
import Box from "@mui/material/Box";
import Drawer from "@mui/material/Drawer";
import List from "@mui/material/List";
import { makeStyles } from "@material-ui/core/styles";
import rightImg from "../components/assets/assets.png";

type pageName = {
  pageName: string;
  isOpen: boolean;
  isOpenFunc: Function;
};

type Anchor = "right";
const useStyles = makeStyles((theme) => ({
  drawerPaper: {
    width: 180,
    backgroundColor: "#292929",
    overflow: "hidden",
  },
}));

const Header = (props: pageName) => {
  const [state, setState] = React.useState({
    right: false,
  });
  console.log(props.isOpen);

  useEffect(() => {
    !!props.isOpen && setState({ ...state, right: true });
  }, [!!props.isOpen]);

  const classes = useStyles();
  const toggleDrawer =
    (anchor: Anchor, open: boolean) =>
    (event: React.KeyboardEvent | React.MouseEvent) => {
      console.log(11111111111111);

      if (
        event.type === "keydown" &&
        ((event as React.KeyboardEvent).key === "Tab" ||
          (event as React.KeyboardEvent).key === "Shift")
      ) {
        return;
      }
      !!props.isOpen && props.isOpenFunc();
      setState({ ...state, [anchor]: open });
    };

  const list = (anchor: Anchor) => (
    <Box
      sx={{ width: 220 }}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <List>
        <Link
          style={{ display: props.pageName === "home" ? "none" : "" }}
          to="/"
        >
          <p style={{ fontSize: "20px", color: "white", margin: "10px" }}>
            Home
          </p>
        </Link>
        <Link
          style={{ display: props.pageName === "minting" ? "none" : "" }}
          to="/Minting"
        >
          <p style={{ fontSize: "20px", color: "white", margin: "10px" }}>
            Minting
          </p>
        </Link>
        <Link
          style={{ display: props.pageName === "lottery" ? "none" : "" }}
          to="/Lottery"
        >
          <p style={{ fontSize: "20px", color: "white", margin: "10px" }}>
            Lottery
          </p>
        </Link>
        <Link
          style={{ display: props.pageName === "staking" ? "none" : "" }}
          to="/Staking"
        >
          <p style={{ fontSize: "20px", color: "white", margin: "10px" }}>
            Staking
          </p>
        </Link>
      </List>
    </Box>
  );

  console.log(window.innerWidth, 555555555111);
  return (
    <div
      style={{
        display: "flex",
        justifyContent: "space-between",
        color: "white",
        alignItems: "center",
        width: "92%",
        margin: "auto",
        marginTop: "20px",
      }}
    >
      <p style={{ fontSize: "25px", fontWeight: 600 }}>Logo</p>
      <div className="headingOptions">
        <Link
          style={{ display: props.pageName === "home" ? "none" : "" }}
          to="/"
        >
          <p className={"landingPageDialog"}>Home</p>
        </Link>
        <Link
          style={{ display: props.pageName === "lottery" ? "none" : "" }}
          to="/Lottery"
        >
          <p className={"landingPageDialog"}>Lottery</p>
        </Link>
        <Link
          style={{ display: props.pageName === "minting" ? "none" : "" }}
          to="/Minting"
        >
          <p className={"landingPageDialog"}>Minting</p>
        </Link>
        <Link
          style={{ display: props.pageName === "staking" ? "none" : "" }}
          to="/Staking"
        >
          <p className={"landingPageDialog"}>Staking</p>
        </Link>
        <img
          className="drawerContainer"
          onClick={toggleDrawer("right", true)}
          style={{
            cursor: "pointer",
            height: "44px",
            width: "44px",
            borderRadius: "22px",
          }}
          src={rightImg}
          alt="Profile Photo"
        />
        <img
          className="emptyDrawer"
          style={{
            height: "44px",
            width: "44px",
            borderRadius: "22px",
          }}
          src={rightImg}
          alt="Profile Photo"
        />
      </div>
      <Drawer
        className="myDrawer"
        classes={{
          paper: classes.drawerPaper,
        }}
        anchor={"right"}
        open={state.right && window.innerWidth < 769}
        onClose={toggleDrawer("right", false)}
      >
        {list("right")}
      </Drawer>
    </div>
  );
};

export default Header;
